import * as THREE from 'three';
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import StarField from "./StarField";

const solarSystemCanvas = document.querySelector('.solar-system');

if (solarSystemCanvas) {
    // scene
    const scene = new THREE.Scene();

    //
    const starfield = new StarField(scene);

    // textureLoader
    const textureLoader = new THREE.TextureLoader();

    // textures
    const sunTexture = textureLoader.load('img/solar/sun.jpg');
    const mercuryTexture = textureLoader.load('img/solar/mercury.png');
    const venusTexture = textureLoader.load('img/solar/venus.jpg');
    const earthTexture = textureLoader.load('img/solar/earth.jpg');
    const marsTexture = textureLoader.load('img/solar/mars.jpg');
    const moonTexture = textureLoader.load('img/solar/moon.jpg');

    //
    const sphereGeometry = new THREE.SphereGeometry(1, 32, 32);
    const sunMaterial = new THREE.MeshBasicMaterial(
        {
            map: sunTexture,
        }
    )

    const sun = new THREE.Mesh(
        sphereGeometry,
        sunMaterial
    )

    //
    const mercuryMaterial = new THREE.MeshStandardMaterial({
        map: mercuryTexture,
    })

    const venusMaterial = new THREE.MeshStandardMaterial({
            map: venusTexture,
    })

    const earthMaterial = new THREE.MeshStandardMaterial({
            map: earthTexture,
    })

    const marsMaterial = new THREE.MeshStandardMaterial({
            map: marsTexture,
    })

    const moonMaterial = new THREE.MeshStandardMaterial({
            map: moonTexture,
    })

    sun.scale.setScalar(5);
    scene.add(sun);

    const planets = [
        {
            name: 'Mercury',
            radius: 0.5,
            distance: 10,
            speed: 0.01,
            material: mercuryMaterial,
            moons: []
        },
        {
            name: 'Venus',
            radius: 0.8,
            distance: 15,
            speed: 0.007,
            material: venusMaterial,
            moons: []
        },
        {
            name: 'Earth',
            radius: 1,
            distance: 20,
            speed: 0.005,
            material: earthMaterial,
            moons: [
                {
                    name: 'Moon',
                    radius: 0.3,
                    distance: 3,
                    speed: 0.015
                }
            ]
        },
        {
            name: 'Mars',
            radius: 0.7,
            distance: 25,
            speed: 0.003,
            material: marsMaterial,
            moons: [
                {
                    name: 'Phobos',
                    radius: 0.1,
                    distance: 2,
                    speed: 0.02
                },
                {
                    name: 'Deimos',
                    radius: 0.2,
                    distance: 3,
                    speed: 0.015,
                    color: 0xffffff,
                },
            ]
        },
    ];


    const createPlanet = (planet) => {
        const planetMesh = new THREE.Mesh(
            sphereGeometry,
            planet.material
        )

        planetMesh.scale.setScalar(planet.radius);
        planetMesh.position.x = planet.distance;

        return planetMesh;
    }
    const createMoon = (moon) => {
        const moonMesh = new THREE.Mesh(
            sphereGeometry,
            moonMaterial
        )
        moonMesh.scale.setScalar(moon.radius);
        moonMesh.position.x = moon.distance;

        return moonMesh;
    }

    const planetMeshes = planets.map((planet, indexPlanet) => {
        const planetMesh = createPlanet(planet);
        scene.add(planetMesh)
        planetMesh.name = planet.name;

        planet.moons.forEach((moon, indexMoon) => {
            const moonMesh = createMoon(moon)
            planetMesh.add(moonMesh);
        })

        return planetMesh;
    })


    // light
    const ambientLight = new THREE.AmbientLight(
        0xffffff,
        0.1
    )
    scene.add(ambientLight);

    const pointLight = new THREE.PointLight(
        0xffffff,
        400
    )

    scene.add(pointLight)

    const activePlanetLight = new THREE.DirectionalLight(
        0xffffff,
        1
    )
    activePlanetLight.position.z  = 2


    // stars


    // camera
    const camera = new THREE.PerspectiveCamera(
        50,
        window.innerWidth / window.innerHeight,
        10,
        16000
    )

    // const axesHelper = new THREE.AxesHelper( 50 );
    // scene.add( axesHelper );

    camera.position.z = 40;

    // buttons
    const menu = document.querySelector('.jsMenu')

    planets.forEach(planet => {
        var btn = document.createElement('button');
        btn.classList.add('jsMenuButton')
        btn.textContent = planet.name;
        menu.appendChild(btn);
    })
    const buttons = document.querySelectorAll('.jsMenuButton');

    let activePlanet;

    if (buttons.length) {
        buttons.forEach((btn, index) => {
            btn.addEventListener('click', function (e) {
                if (index === 0) {
                    activePlanet = undefined;
                    scene.remove(activePlanetLight);
                } else {
                    activePlanet = index - 1;
                    scene.add(activePlanetLight);

                    // planetMeshes.forEach((planet, planetIndex) => {
                    //     if (planetIndex === activePlanet) {
                    //         planet.scale.setScalar(1)
                    //     }
                    // })
                }
            })
        })
    }
    // buttons ENDS


    // render
    const renderer = new THREE.WebGLRenderer({
        canvas: solarSystemCanvas,
        antialias: true,
    })

    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

    // controls
    const controls = new OrbitControls(camera, solarSystemCanvas);
    controls.enableDamping = true;
    controls.maxDistance = 200;
    controls.minDistance = 20;

    window.addEventListener('resize', function () {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(window.innerWidth, window.innerHeight);
    })



    const tick = () => {

        // animation

        planetMeshes.forEach((planet, planetIndex) => {
            planet.rotation.x += planets[planetIndex].speed;
            planet.position.x = Math.sin(planet.rotation._x * 1.1) * planets[planetIndex].distance;
            planet.position.z = Math.cos(planet.rotation._x * 1.1) * planets[planetIndex].distance;

            planet.children.forEach((moon, moonIndex) => {
                moon.rotation.x += planets[planetIndex].moons[moonIndex].speed;
                moon.position.x = Math.sin(moon.rotation.x) * planets[planetIndex].moons[moonIndex].distance
                moon.position.z = Math.cos(moon.rotation.x) * planets[planetIndex].moons[moonIndex].distance
            })
        })


        if (activePlanet !== undefined) {
            camera.position.x = Math.sin(planetMeshes[activePlanet].rotation._x * 1.1) * (planets[activePlanet].distance + 11);
            camera.position.z = Math.cos(planetMeshes[activePlanet].rotation._x * 1.1) * (planets[activePlanet].distance + 11);
            camera.position.y = 0

            activePlanetLight.position.x = Math.sin(planetMeshes[activePlanet].rotation._x * 1.1) * (planets[activePlanet].distance + 11);
            activePlanetLight.position.z = Math.cos(planetMeshes[activePlanet].rotation._x * 1.1) * (planets[activePlanet].distance + 11);
        }



        //
        controls.update();
        renderer.render(scene, camera);
        window.requestAnimationFrame(tick)
    }

    tick();
}
