import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';


function init () {
    // 
    const scene = new THREE.Scene();
    const canvas = document.querySelector('.canvas-main');

    if (!canvas) {
        return
    }

// 
    const sizes = {
        width: window.innerWidth,
        height: window.innerHeight,
    };

    const cursor = {
        x: 0,
        y: 0
    }

    const camera = new THREE.PerspectiveCamera(
        75,
        sizes.width / sizes.height);
    camera.position.z = 3;

    const controls = new OrbitControls(camera, canvas);

    scene.add(camera);

    // init load texture
    const textureLoader = new THREE.TextureLoader();


    // obj
    const sphereGeometry =  new THREE.SphereGeometry(0.5,32,32);
    const planeGeometry = new THREE.PlaneGeometry(1,1)


    // texture

    const grassAlbedo = textureLoader.load('img/whispy-grass-meadow-bl/wispy-grass-meadow_albedo.png')
    const grassRoughness = textureLoader.load('img/whispy-grass-meadow-bl/wispy-grass-meadow_roughness.png')
    const grassMetall = textureLoader.load('img/whispy-grass-meadow-bl/wispy-grass-meadow_metallic.png')
    const grassNormal = textureLoader.load('img/whispy-grass-meadow-bl/wispy-grass-meadow_normal-ogl.png')
    const grassHeight = textureLoader.load('img/whispy-grass-meadow-bl/wispy-grass-meadow_height.png')

    // textureGrass.repeat.set(4,4);
    // textureGrass.wrapS = THREE.RepeatWrapping;
    // textureGrass.wrapT = THREE.RepeatWrapping;
    // textureGrass.wrapS = THREE.MirroredRepeatWrapping;
    // textureGrass.wrapT = THREE.MirroredRepeatWrapping;



    //material
    // const material = new THREE.MeshStandardMaterial({
        // color: 'white',
        // wireframe: true,
        // transparent: true,
        // opacity: 0.3
    // });

    const material = new THREE.MeshStandardMaterial();

    material.side = THREE.DoubleSide;
    // material.map = grassAlbedo;
    // material.roughnessMap = grassRoughness;
    // material.roughness = 1;
    // material.metalnessMap = grassMetall;
    // material.metalness = 1;
    // material.normalMap = grassNormal;
    // material.displacementMap = grassHeight;
    // material.displacementScale = 0.1;


    const sphereMesh = new THREE.Mesh(sphereGeometry, material);
    const plane = new THREE.Mesh(planeGeometry, material)

    plane.position.x = -1.5;

    // plane.scale.set(10,10,10)
    plane.side = 2;

    scene.add(plane, sphereMesh);



    // const fog = new THREE.Fog(0xffffff, 1, 10);
    // scene.fog = fog;
    // scene.background = new THREE.Color(0xffffff)
    const light = new THREE.AmbientLight( 0xffffff, 1);
    scene.add( light );

    // const hemisphereLight = new THREE.HemisphereLight(
    //     'red',
    //     'blue',
    //     0.5
    // )

    // scene.add(hemisphereLight)

    // const pointLight = new THREE.PointLight(0xffffff,1);
    // const pointLight2 = new THREE.PointLight(0xffffff,1);
    // pointLight.position.set(-5,5,5)
    // scene.add(pointLight);
    // scene.add(pointLight2);
    // scene.add(axesHelper);

    const renderer = new THREE.WebGLRenderer({ canvas });
    renderer.setSize(sizes.width, sizes.height);
    renderer.render(scene, camera);

    window.addEventListener('resize', (e) => {
       camera.aspect = window.innerWidth / window.innerHeight;
       camera.updateProjectionMatrix();
       renderer.setSize(sizes.width, sizes.height);
    })


    const clock = new THREE.Clock();
    let previousTime = 0

    const tick = (e) => {

        // const currentTime = clock.getElapsedTime();
        // const delta = currentTime - previousTime;
        // previousTime = currentTime;
        //
        //
        // controls.update();
        renderer.render(scene, camera);
        window.requestAnimationFrame(tick)
    }

    tick();

}


init()
