import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';


function init () {
    // 
    const scene = new THREE.Scene();
    const canvas = document.querySelector('.canvas-some');

    if (!canvas) {
        return
    }

    const sizes = {
        width: window.innerWidth,
        height: window.innerHeight,
    };

    const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height);
    camera.position.z = 4;

    const controls = new OrbitControls(camera, canvas);

    scene.add(camera);

    // const geometry = new THREE.BoxGeometry(1, 1, 1);
    // const geometry = new THREE.CircleGeometry(1,20, 0, Math.PI * 5);
    // const geometry = new THREE.CircleGeometry(1,20, 0, Math.PI * 5);



    const geometry = new THREE.BufferGeometry();

    const amount = 18;

    const points = new Float32Array(amount * 3 * 3)

    for (let i = 0; i < amount * 3 * 3; i++) {
        points[i] = (Math.random() ) * 4;
    }


    const pointsBuffer = new THREE.BufferAttribute(points, 3);

    geometry.setAttribute('position',pointsBuffer)

    const material = new THREE.MeshBasicMaterial({
        color: 'red',
        wireframe: true,
    });

    const mesh = new THREE.Mesh(geometry, material);


    scene.add(mesh);
    const renderer = new THREE.WebGLRenderer({ canvas });
    renderer.setSize(sizes.width, sizes.height);
    renderer.render(scene, camera);

    const tick = () => {

        renderer.render(scene, camera);
        window.requestAnimationFrame(tick)
    }

    tick();

}


init()
